@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Caveat&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Fredericka+the+Great&display=swap);
body{
  background-color: rgba(0, 0, 0, 0.8);
  text-align: center;
  -webkit-animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
          animation: fade-in 1.2s cubic-bezier(0.390, 0.575, 0.565, 1.000) both;
          font-size: 1rem;
}

@-webkit-keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

@keyframes fade-in {
  0% {
    opacity: 0;
  }
  100% {
    opacity: 1;
  }
}

.nav{
  margin-top: 1rem;
  margin-left: 0.3rem;
  cursor: pointer;

}
.navbar{
  background-color: white;
  padding: 0.2rem;
  width: 2.5%;
  margin-top: 0.3rem;
}
.navMenuBox{
  background-color: rgba(250, 250, 250, 0.5);
  margin: 0.5rem 0;
  padding: 0.5rem 0;
}
.navMenuBox2{
/*  background-color: rgba(250, 250, 250, 1);*/
  margin: 0.5rem 0;
  padding: 0.5rem 0;
}
.h1{
  font-size: 5rem;
  color: white;
  text-decoration: underline;
  margin-bottom: 5rem;
  font-family:  'Fredericka the Great', cursive;
  -webkit-animation: tracking-in-contract 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;
          animation: tracking-in-contract 1.5s cubic-bezier(0.215, 0.610, 0.355, 1.000) both;

}
@-webkit-keyframes tracking-in-contract {
  0% {
    letter-spacing: 1em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    letter-spacing: normal;
    opacity: 1;
  }
}
@keyframes tracking-in-contract {
  0% {
    letter-spacing: 1em;
    opacity: 0;
  }
  40% {
    opacity: 0.6;
  }
  100% {
    letter-spacing: normal;
    opacity: 1;
  }
}

.container{
  display: grid;
  grid-template-columns: 20rem 20rem 20rem 20rem;
  justify-content: center;

}

.polaroid{
  background: #ffffff;
  display: inline-block;
  padding: 1rem 1rem 0.3rem;
  margin-top: 8rem;
  text-align: center;
  text-decoration: none;
  box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, .3);
  cursor: pointer;

}
.polaroid:hover{
  background: #000;
}
.polaroid:hover h3{color: #fff;}
.polaroid:hover p{color: #fff;}
.polaroid2{
  background: #000;
  display: inline-block;
  padding: 1rem 1rem 0.3rem;
  text-align: center;
  text-decoration: none;
  box-shadow: 0 0.3rem 0.5rem rgba(0, 0, 0, .3);
  cursor: pointer;
  margin: 0 1rem;

}
.polaroid3{
  z-index: 1001;
  position: absolute;
  -webkit-transform: scale(2.0);
          transform: scale(2.0);
  top: 70rem;
  left: 25rem;
  cursor: pointer;
 background: rgb(0 0 0 / 80%);

}
.centerImg{
  padding-bottom: 3rem;
  -webkit-transform: scale(1.0);
          transform: scale(1.0);
}

.centerXLImg{
  padding: 7rem 1rem;
}


h3{
  color: #333;
  font-size: 2.5rem;
  content: attr(title);
  position: relative;
  font-family: 'Caveat', cursive;
  font-weight: bolder;
  margin-bottom: 0;
  margin-top: 0.5rem;
}
p{
  font-family: 'Caveat', cursive;
  margin: 0 auto 0.5rem auto;
  font-weight: bold;
  font-size: 1.3rem;

}

img{
  width: 13rem;
  height: 17rem;

}
.passportImg{
  width: 45rem;
  height: 35rem;
}
.bigImg{
  width: 13rem;
  height: 18rem;
  margin-top: 1rem;
  border: 3px solid black;
  position: absolute;
  overflow: visible;
  right: 51rem;
  top: 8rem;
}
.bigImg2{
  width: 13rem;
  height: 18rem;
  margin-top: 1rem;
  border: 3px solid black;
}
.pawImg{
  width: 5rem;
  height: 5rem;
  position: absolute;
  right: 30rem;
  top: 28rem;


}
.containerSocial{
  margin-top: 4rem;
}

.aSocial{
   padding: 0 0.5rem;
}

.petpage{
  display: block;
  background-color: rgba(250, 250, 250, 1);
  margin-top: 0.5rem;
}
ul{
  overflow: hidden;
  position: absolute;
  z-index: 1;
}
li{
  list-style-type: none;
  background-color: rgba(250, 250, 250, 1);
  width: 14rem;
  padding: 1rem 0.5rem;
  margin: 0.5rem;
  border: 1px solid grey;
}
li:hover{
  opacity: 0.5;
}
li:hover .navlink{
  color: orange;
}
.navlink{
  text-decoration: none;
  color: black;
  font-size: 1.5rem;
}

.p{
  margin-top: 2rem;
  font-size: 1.5rem;
  font-family: 'Open Sans', sans-serif;
  width: 70%;
  line-height: 2.5;
  padding-bottom: 4rem;
  text-align: left;

}
.p2{
  margin-top: 2rem;
  font-size: 1.5rem;
  font-family: 'Open Sans', sans-serif;
  width: 70%;
  line-height: 1.9;
  padding: 2rem;
  text-align: left;
  background-color: rgba(221,195,238, 1);
  color: black;
  border-radius: 20px;

}

button{
  color: orange;
  background: linear-gradient(to bottom right, white, #f5f5dc);;
  cursor: pointer;
  width: 10rem;
  height: 3rem;
  font-size: 1.3rem;
  margin: 0 0 2rem 0;
  border-radius: 30px;
  border: 1px solid orange;
  box-shadow: 0 0 50px orange;
 /* box-shadow: 0 -5px 3px -3px black, 0 5px 3px -3px black;*/;
}
a{
  color: black;
}
.adoptContainer{
  display: flex;
  justify-content: center;

}
.adoptImg{
  margin-top: 2rem;
  box-shadow: 1.5rem 1.5rem black, -1.5rem -1.5rem;
}
.adoptBox{
  display: block;
  margin: 5rem 0 0 3rem;
}
.h2Adopt{
  font-family: 'Open Sans', sans-serif;
  margin-bottom: 5rem;
  color: white;
  text-decoration: underline;
}
form{
  margin: 5rem 0;
}
.formContainer{
  display: grid;
  justify-content: center;
  grid-template-columns: 18rem 18rem 18rem 18rem;
  grid-row-gap: 2rem;
}
label{
  font-size: 1.4rem;
  color: white;
}
input{
  font-size: 1rem;
  height: 1.5rem;
}
.questions{
  margin-top: 5rem;
 display: grid;
 grid-template-columns: 37rem 38rem;
 grid-row-gap: 2rem;

}
.checkbox{
  padding: 0 0.5rem;
}
.submit{
  margin-top: 6rem;

}
.btn{
  width: 8rem;
  height: 3rem;
  border-radius: 30px;
  font-size: 1.2rem;
  box-shadow: 0 8px 8px -4px grey;
  cursor: pointer;
}
.particles{
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;

}
@media only screen and (min-device-width: 280px)and (max-device-width: 767px) {
  html{font-size:0.8rem;}
  .navbar{width:  9%;}
  li{margin: 0;}
  .container{grid-template-columns: 12rem 12rem;}
  img{width: 7rem; height: 10rem;}
  .polaroid{margin-top: 2rem;}
  .passportImg{width:27rem ; height: 23rem;}
  .bigImg{width: 7rem; height: 10rem;right: 18rem; top:  5rem}
  .pawImg{width: 3rem; height: 3rem; right: 5rem; top: 20rem;}
  .middleImgBox{ display: flex; flex-wrap: wrap; justify-content: center;}
  .polaroid3{left: 28%; top: 130rem; display: flex; flex-wrap: wrap;flex-direction: column;}
  .centerXLImg{padding: 1rem 3rem;}
  .formContainer{grid-template-columns: 12rem 15rem;}
  .questions{grid-template-columns: 13rem 14rem;}
}

@media only screen and (min-device-width:768px)and (max-device-width: 1365px){
  html{font-size:1.2rem;}
  .navbar{width:  9%;}
  li{margin: 0;}
  .container{grid-template-columns: 12rem 12rem 12rem;}
  img{width: 9rem; height: 11rem;}
  .polaroid{margin-top: 2rem;}
  .passportImg{width:27rem ; height: 23rem;}
  .bigImg{width: 7rem; height: 10rem;right: 57%; top:  12%;}
  .pawImg{width: 3rem; height: 3rem; right: 30%; top: 20rem;}
  .middleImgBox{ display: flex; flex-wrap: wrap; justify-content: center;}
  .polaroid3{left: 32%; top: 106rem; display: flex; flex-wrap: wrap;flex-direction: column;}
  .centerXLImg{padding: 1rem 3rem;}
  .formContainer{grid-template-columns: 15rem 18rem;}
  .questions{grid-template-columns: 35rem;}
}
@media only screen and (min-device-width:1900px) {
  html{font-size: 1.5rem;}
}



 
h1{
	font-family: 'Caveat', cursive;
	font-size: 3.5rem;
	margin: 0rem;
	

}
h2{
	font-family: 'Caveat', cursive;
	font-size: 2.5rem;
	margin: 0rem;
}
h4{
	font-family: 'Caveat', cursive;
	font-size: 2rem;
	margin: 0;
}



